import React from 'react'
import ComTab from './components/com-tab/ComTab.jsx'

import { useTranslation } from "react-i18next";
export default function Image(props) {
    const { t } = useTranslation();
    const menus = [
        {
            title: '快照',
            to: 'snapshot'
        },
        {
            title: '备份',
            to: 'backup'
        },
        {
            title: '模板',
            to: 'template'
        },
        // {
        //     title: '自定义ISO',
        //     to: 'iso'
        // }
    ]
    return (
        <>
            <ComTab menus={menus}></ComTab >
        </>
    )
}
